import React, { Component } from "react";
import Highcharts from "highcharts";
import addHighchartsMore from "highcharts/highcharts-more";
import addVariwideModule from "highcharts/modules/variwide";
import {
  Chart,
  HighchartsChart,
  VariwideSeries,
  withHighcharts,
  XAxis,
  ColumnSeries,
  SplineSeries,
  YAxis,
  Legend,
  Title,
  PieSeries
} from "react-jsx-highcharts";

import ReactResizeDetector from "react-resize-detector";
import styled from "styled-components";
import "styled-components";
require("highcharts/modules/exporting")(Highcharts);

export const StyledVariwide = styled.div`
  position: relative;
  overflow: hidden; // hide the overflowing parts of the chart during resize

  .chart-container {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
  }
`;
const pieData = [
                      {
                        name: "Argon",
                        y: 100.9,
                        color: "#3498db"
                      },
                      {
                        name: "Nitrogen",
                        y: 78.1,
                        color: "#9b59b6"
                      },
                      {
                        name: "Oxygen",
                        y: 20.9,

                        color: "#2ecc71"
                      },
                      {
                        name: "Trace Gases",
                        y: 70.1,
                        x: 20,
                        color: "#f1c40f"
                      }
                    ];

addHighchartsMore(Highcharts);
addVariwideModule(Highcharts);

class Variwide extends Component {
  state = {
    height: 200,
    width: 0
  };

  onResizeChart = (width, height) => {
    console.log(`chart size: ${width}x${height}`);
    this.setState({ width: Math.floor(width), height: Math.floor(height) });
  };

  render() {
      const data =  this.props.data
      const colors = ["rgba(0,0,255,0.3)","rgba(0,0,255,0.4)","rgba(0,0,255,0.5)","rgba(0,0,255,0.6)","rgba(0,0,255,0.7)","rgba(0,0,255,0.8)","rgba(0,0,255,0.9)"]
      const workflowMasterName =  this.props.workflowMasterName
      var categories = []
      var shown_data = []
      var x_val = 0
      var index = 0
      data.map(value =>{
          categories.push(value.name)
          if(index > colors.length){
              index =0
          }
          shown_data.push({x: x_val, y:value.data[0], z: 10, name:value.name,  color:colors[index] })

          x_val = x_val +10.1
          index ++;
      })

      return (
      <StyledVariwide>
        <div className="chart-container">
          <HighchartsChart>
            <Chart
              height={this.state.height}
              width={this.state.width}
              backgroundColor={"transparent"}
            />
              <Title>{workflowMasterName}</Title>

            <Legend />
            <XAxis>

              {/*<VariwideSeries
                              data={[
                                {
                                  x: 0,
                                  y: 50,
                                  z: 5,
                                  name: "test 1",
                                  color: "rgba(0,0,255,0.4)"
                                },
                                {
                                  x: 5,
                                  y: 40,
                                  z: 10,
                                  name: "test 2",
                                  color: "rgba(0,0,255,0.5)"
                                },
                                {
                                  x: 15,
                                  y: 30,
                                  z: 10,
                                  name: "test 3",
                                  color: "rgba(0,0,255,0.6)"
                                },
                                {
                                  x: 25,
                                  y: 60,
                                  z: 5,
                                  name: "test 4",
                                  color: "rgba(0,0,255,0.7)"
                                }
                              ]}
                            />*/}
                <VariwideSeries  name={categories[0]} data={shown_data}  />
            </XAxis>
            <YAxis />
          </HighchartsChart>

          <ReactResizeDetector
            handleWidth
            handleHeight
            onResize={this.onResizeChart}
          />
        </div>
      </StyledVariwide>
    );
  }
}

export default withHighcharts(Variwide, Highcharts);


