class Spline extends Component {
  state = {
    height: 200,
    width: 0
  };

  onResizeChart = (width, height) => {
    console.log(`chart size: ${width}x${height}`);
    this.setState({ width: Math.floor(width), height: Math.floor(height) });
  };

  render() {
    const data =  this.props.data
    const workflowMasterName =  this.props.workflowMasterName
    var categories = []
    var data_show = []
    data.map(value =>{
      categories.push(value.name)
      data_show.push(value.data)
    })

    return (
      <StyledSpline>
        <div className="chart-container">
          <HighchartsChart>
            <Chart
              height={this.state.height}
              width={this.state.width}
              backgroundColor={"transparent"}
            />
            <Title>{workflowMasterName}</Title>

            <Legend />
            <YAxis>
               {/*<SplineSeries
                            name="Total consumption"
                            data={pieData}
                          />*/}

                <SplineSeries  data={data_show}  />

            </YAxis>
            <XAxis id="x" />
          </HighchartsChart>

          <ReactResizeDetector
            handleWidth
            handleHeight
            onResize={this.onResizeChart}
          />
        </div>
      </StyledSpline>
    );
  }
}

export default withHighcharts(Spline, Highcharts);
